// Colors
$white-color: #FFFFFF;
$black-color: #000000;


// Основний колір тексту
$primary-text-color:#907E82;

//Колір заголовків і більшості кнопок, іконки соцсетей при говері, текст світлих кнопок
$primary-accent-color: #D41443; 

// Альтернативний колір кнопок
$secondary-accent-color: #D41342;

// Колір вторинних заголовків
$subtitle-color: #FFA5BA;

// Фон секцій
$section-bg-primary: #E5E5E5;
$section-bg-secondary: #FFF5F6;
$general-bg-color: #fff5f6;

// Фон героя
$pink-bg-primary: #FFB8CA;

// Фон мобільного меню
$pink-bg-secondary: #E17992;

// Колір кнопок при ховері
$btn-hover: #C7133E;

// Вторинний колір кнопок
$secondary-button-color: #ECEBEB;

//Іконки соцмереж
$social-primary-color: #FEA5BB;


// Product cards
$product-card-primary: #F0D1A5;
$product-card-secondary: #C2E297; 


$navigation-dots-color: #D9D9D9;
$slider-quotes: #E1E1E1;
$primary-box-shadow: rgba(212, 20, 67, 0.1);


// Змінні для шрифта
$font-family: DM Sans, sans-serif;
$font-family-second: Titan One, sans-serif;
$font-size: 14px;


// Анімашка
$transition-duration: 250ms;
$timing-function: cubic-bezier(0.4, 0, 0.2, 1);